* {
  box-sizing: border-box;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  width: 100vw;
  height: 100vh;
  background-image: url("../backgr.e5118e5d.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Bauhaus 93", sans-serif;
  font-size: 1.6rem;
  overflow: hidden;
}

main {
  margin: 0 auto;
  padding: 2rem;
}

#questions {
  width: 300px;
  margin-bottom: 10px;
  margin-left: 50px;
  position: absolute;
  bottom: 0;
}

#questions h2 {
  font-size: 14px;
}

label {
  font-size: 40px;
}

input[type="text"], input[type="email"], input[type="number"] {
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #292929;
  border-radius: 15px;
  margin-bottom: 1rem;
  padding: 1rem;
  font-size: 1.6rem;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #ce1110;
  border: none;
  border-radius: 15px;
  margin-right: 120px;
  padding: 4px;
}

button:hover {
  background-color: #ce1110;
}

#prizes {
  margin-top: 6%;
  margin-bottom: 4rem;
}

#prizes h2 {
  margin-bottom: 1rem;
  font-size: 2.4rem;
}

.boxes {
  justify-content: space-around;
  padding: 0 20px;
  display: flex;
}

.box {
  width: 200px;
  height: 200px;
  cursor: pointer;
  background-image: url("../closed.5da6905a.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: transform .3s;
}

.box:hover {
  transform: scale(1.1);
}

.box.opened {
  background-image: url("../opened.9b0528a4.png");
}

.smears {
  width: 1000px;
  position: absolute;
  bottom: -120px;
  right: -120px;
}

.conditions {
  width: 400px;
  color: #292929;
  text-align: center;
  position: absolute;
  bottom: 50px;
  right: 160px;
}

.input-thumb, .input-thumb2, .input-thumb3 {
  text-align: center;
  gap: 30px;
  margin-bottom: 20px;
  display: flex;
}

@font-face {
  font-family: "Bauhaus 93";
  src: url("../Bauhaus 93 Regular.6937a785.ttf");
}

